body,
html {
  margin: 0;
  padding: 0;
  height: 100%;
  background-color: #f0f0f0;
  font-family: Arial, sans-serif;
}

.question-page {
  display: grid;
  grid-template-rows: auto 1fr auto;
  height: 100vh;
  padding: 20px;
  box-sizing: border-box;
}

header {
  display: grid;
  grid-template-columns: auto 1fr auto;
  align-items: center;
  background-color: white;
  padding: 15px 20px;
  border-radius: 22px;
}

.icon-button {
  display: flex;
  background: none;
  border: none;
  font-size: 24px;
  cursor: pointer;
  color: #333;
}

.end-round-button {
  background-color: #f0f0f0;
  border: none;
  padding: 10px 20px;
  border-radius: 10px;
  cursor: pointer;
  font-weight: bold;
}

main {
  display: grid;
  grid-template-columns: 1fr 1fr;
  background-color: #f1f1f1;
  border-radius: 22px;

  padding-top: 20px;
  padding-bottom: 20px;
}

.question-container {
  width: 90%;
  background-color: white;
  padding: 20px;
  /* border-radius: 10px 20px; */
  border-radius: 28px;
}
.question {
  width: 100%;
}
.options-container {
  background-color: white;
  padding: 20px;
  border-radius: 28px;
}

.question-container h2 {
  margin-top: 0;
  font-size: 24px;
}

.options-container h3 {
  margin-top: 0;
  font-size: 18px;
}

.option {
  display: grid;
  grid-template-columns: auto 1fr;
  align-items: center;
  background-color: white;
  border: 1px solid #e0e0e0;
  border-radius: 25px;
  padding: 10px 20px;
  margin: 10px 0;
  cursor: pointer;
  width: 50%;
}

.option input[type="radio"] {
  margin-right: 10px;
}

footer {
  display: grid;
  grid-template-columns: auto auto;
  justify-content: space-between;
  background-color: white;
  padding: 10px 20px;
  border-radius: 22px;
}

.nav-button {
  background-color: white;
  border: 1px solid #e0e0e0;
  padding: 10px 20px;
  border-radius: 20px;
  cursor: pointer;
  font-weight: bold;
}
